@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
body {
  /* margin: 0; */
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


* {
  font-family: 'Righteous', cursive;
  padding: 5px;
  box-sizing: border-box;

}

.card1 {
  position: relative;
  width: 500px;
  height: 600px;
}
.box1 {
  position: absolute;
  top: 20px;
  right: 20px;
  bottom: 20px;
  left: 20px;
  background: whitesmoke;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 1.5s;
}
.content1 {
  padding: 20px;
  text-align: center;
}

.name1 {
  font-size: 1.8em;
  color: #777;
  z-index: 1;
  transition: 1.5s;
}

.para1 {
  font-size: 1em;
  color: #777;
  z-index: 1;
  transition: 1.5s;
}

.anker1 {
  position: relative;
  display: inline-block;
  padding: 8px 20px;
  background: #777;
  margin-top: 15px;
  border-radius: 20px;
  color: #363636;
  text-decoration: none;
}

.card1:hover .box1 {
  -webkit-transform: translateY(-50px);
          transform: translateY(-50px);
  background: linear-gradient(45deg, #ffff,whitesmoke);
  border-radius: 15;
}

.box1 .content1 h3,
.box1 .content1 p {
  color: #363636;
  border-radius: 15;
}


